  line-height: 1.5;
  border-radius: 0.25em;
}

.fc .fc-button:hover {
  text-decoration: none;
}

.fc .fc-button:focus {
  outline: 0;
  box-shadow: 0 0 0 0.2rem rgba(44, 62, 80, 0.25);
}

.fc .fc-button:disabled {
