.dd_toast_container {
  width: 300px;
  position: fixed;
  z-index: 10000;
}

.dd_toast_container.topright {
  top: 20px;
  right: 20px;
}

.dd_toast_container.botright {
  bottom: 20px;
  right: 20px;
}

.dd_toast_container.topleft {
  top: 20px;
  left: 20px;
}

.dd_toast_container.botleft {
  bottom: 20px;
  left: 20px;
}

.dd_toast_container.topcenter {
  left: 50%;
  margin-left: -150px;
  top: 20px;
}

.dd_toast_container.center {
  left: 50%;
  margin-left: -150px;
  top: 50%;
}

.dd_toast_container.botcenter {
  left: 50%;
  margin-left: -150px;
  bottom: 20px;
}

.dd_toast_container .dd_toast {
  margin-bottom: 10px;
  display: none;
  color: #d6daa1;
  font-weight: bold;
  padding: 16px;
  border-radius: 4px;
  box-shadow: 0 0 8px #000;
  z-index: 10000;
  border: 1px solid #327967;
  background-color: #327967;
  background-image: -moz-linear-gradient(#327967, #1d5042);
  background-image: -webkit-linear-gradient(#327967, #1d5042);
  background-image: linear-gradient(#327967, #1d5042);
}

