/* -------------------------------------------------------------------------------------------------------------- */
/* css for loading progress bar ----------------------------------------------------------------------------------*/
/* -------------------------------------------------------------------------------------------------------------- */
#progress {
  position: fixed;
  z-index: 2147483647;
  top: 0;
  left: -6px;
  width: 0%;
  height: 2px;
  background: #a8b355;
  /* background:#462200 url('../../images/admin/fadmin_background.jpg'); */
  -moz-border-radius: 1px;
  -webkit-border-radius: 1px;
  border-radius: 1px;
  -moz-transition: width 500ms ease-out, opacity 400ms linear;
  -ms-transition: width 500ms ease-out, opacity 400ms linear;
  -o-transition: width 500ms ease-out, opacity 400ms linear;
  -webkit-transition: width 500ms ease-out, opacity 400ms linear;
  transition: width 500ms ease-out, opacity 400ms linear;
}

#progress.done {
  opacity: 0;
}

#progress dd, #progress dt {
  position: absolute;
  top: 0;
  height: 2px;
  -moz-box-shadow: #a8b355 1px 0 6px 1px;
  -ms-box-shadow: #a8b355 1px 0 6px 1px;
  -webkit-box-shadow: #a8b355 1px 0 6px 1px;
  box-shadow: #a8b355 1px 0 6px 1px;
  -moz-border-radius: 100%;
  -webkit-border-radius: 100%;
  border-radius: 100%;
}

#progress dd {
  opacity: 1;
  width: 20px;
  right: 0;
  clip: rect(-6px, 22px, 14px, 10px);
}

#progress dt {
  opacity: 1;
  width: 180px;
  right: -80px;
  clip: rect(-6px, 90px, 14px, -6px);
}

@-moz-keyframes pulse {
  30% {
    opacity: 1;
  }
  60% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@-ms-keyframes pulse {
  30% {
    opacity: 0.6;
  }
  60% {
    opacity: 0;
  }
  100% {
    opacity: 0.6;
  }
}
@-o-keyframes pulse {
  30% {
    opacity: 1;
  }
  60% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@-webkit-keyframes pulse {
  30% {
    opacity: 0.6;
  }
  60% {
    opacity: 0;
  }
  100% {
    opacity: 0.6;
  }
}
@keyframes pulse {
  30% {
    opacity: 1;
  }
  60% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
#progress.waiting dd, #progress.waiting dt {
  -moz-animation: pulse 2s ease-out 0s infinite;
  -ms-animation: pulse 2s ease-out 0s infinite;
  -o-animation: pulse 2s ease-out 0s infinite;
  -webkit-animation: pulse 2s ease-out 0s infinite;
  animation: pulse 2s ease-out 0s infinite;
}

/* -------------------------------------------------------------------------------------------------------------- */
/* css for tabbed_content --------------------------------------------------------------------------------------- */
/* -------------------------------------------------------------------------------------------------------------- */
.tabs-container {
  position: relative;
  overflow: hidden;
}

.tabs-container ul.tabs {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.tabs-container li {
  float: left;
  margin-right: 3px;
  height: 30px;
  white-space: nowrap;
  line-height: 30px;
  padding: 0 6px;
  /*border-bottom: 1px solid #DED6CB;*/
}

.tabs-container li.tab {
  background-color: #a8b355;
  border-radius: 4px 4px 0 0;
  cursor: pointer;
}
.tabs-container li.tab.close {
  font-size: inherit;
  opacity: inherit;
  text-shadow: inherit;
  font-weight: inherit;
}

.tabs-container li.tab:hover {
  background-color: #b6c06b;
}

.tabs-container li.tab.right {
  float: right;
  margin-right: 0px;
  margin-left: 3px;
}

.tabs-container li.tab.selected {
  background-color: #bbc56f;
  border-bottom: 2px solid #bbc56f;
  cursor: default;
}

.tabs-container li.tab.disabled {
  background-color: #c0c0c0;
  cursor: auto;
}

.tabs-container li.tab.hidden {
  display: none;
}

.tabs-container li.tab > .close {
  border-radius: 2px;
  background-color: #225824;
  color: #C3C861;
  padding: 0px 3px 0px 2px;
  margin: 0px 0 0 0px;
  cursor: pointer;
  font-size: 0.65em;
  font-weight: bold;
  top: -8px;
  position: relative;
  right: -4px;
  float: none;
  line-height: inherit;
  opacity: inherit;
  text-shadow: inherit;
}

.tabs-container li.tab > .close:hover {
  color: #fff;
  background-color: #38883B;
}

