}

/* Tipsy style */
.qtip-tipsy {
  background: black;
  background: rgba(0, 0, 0, 0.87);
  color: white;
  border: 0 solid transparent;
  font-size: 11px;
  font-family: "Lucida Grande", sans-serif;
  font-weight: bold;
  line-height: 16px;
  text-shadow: 0 1px black;
}

.qtip-tipsy .qtip-titlebar {
